.course-progress-tile{
    background-color: #F5F5F7;
    height:8rem;
    width:50rem;
    // cursor: pointer;
    background-color: #F5F5F7;
    border-radius: 1.4rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    &__heading{
        font-size: 1.6rem;
        font-weight: 600;
        font-family: "Baloo Bhaina 2", cursive;
        line-height: 1.2rem;
    }
    &__logo{
        display: flex;
        justify-content: center;
        align-items: center;
        height:6rem;
        width: 6rem;
        background-color: #fff;
        padding: 1.2rem;
        border-radius: 1.2rem;
    }
    &__progress{
        
        width: auto;
        height:4rem;
    }
}